.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  width: 700px;
  padding: 15px 0px;
  border: 2px solid #8e0039;
}

.label {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  margin-bottom: 15px;
}

.description {
  margin: 0px;
  text-align: center;
  margin-bottom: 5px;
  color: #03426a;
  font-size: 18px;
  font-weight: 700;
}

.input {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  background-color: #ffc173;
  border: 1px solid #8e0039;
  border-radius: 5px;
  color: #24587a;
  font-size: 16px;
}

.button {
  min-width: 100px;
  font-family: inherit;
  appearance: none;
  border: 1px solid #8e0039;
  border-radius: 5px;
  background: #ffc173;
  color: #24587a;
  padding: 8px 16px;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  color: #0a67a3;
  background: #ff8e00;
}